import {React} from 'jimu-core';
import {Popover, PopoverHeader, PopoverBody} from 'reactstrap';
import { SketchPicker, ColorResult } from 'react-color';

interface Props{
  id: string; //TODO we should not require id.
  color: string;
  onChange?: (color: ColorResult) => void,
  onChangeComplete?: (color: ColorResult) => void,
}

interface State{
  showPicker: boolean
}

export default class ColorPicker extends React.PureComponent<Props, State>{

  constructor(props){
    super(props);
    this.state = {
      showPicker: false
    }
  }

  render(){
    return <div className="color-picker-1">
      <div id={this.props.id} style={{height: '100%', width: '100%', background: this.props.color, cursor: 'pointer'}} onClick={() => this.setState({showPicker: true})}></div>
      <Popover placement="bottom" isOpen={this.state.showPicker} target={this.props.id} toggle={() => this.setState({showPicker: !this.state.showPicker})}>
        <PopoverHeader>Choose Color</PopoverHeader>
        <PopoverBody>
          <SketchPicker color={this.props.color} onChange={this.props.onChange} onChangeComplete={this.props.onChangeComplete}/>
        </PopoverBody>
      </Popover>
      
    </div>
    ;
  }
}
